import {Component, ComponentFactoryResolver, Input, OnInit, Type, ViewChild} from '@angular/core';
import {AdDirective} from '../../directives/ad.directive';
import {Settings} from '../../model/settings';
import {Subscription} from 'rxjs/Subscription';
import {PicoEvent} from 'picoevent';
import {SettingsMessage} from '../../model/settings-message';

@Component({
  selector: 'app-tile-wrapper',
  templateUrl: './tile-wrapper.component.html',
  styleUrls: ['./tile-wrapper.component.css']
})
export class TileWrapperComponent implements OnInit {
  @ViewChild(AdDirective) adHost: AdDirective;

  get component(): Type<any> {
    // transform value for display
    return this._component;
  }

  @Input() title: string;
  @Input()
  set component(component: Type<any>) {
    console.log('changed');
    this._component = component;
    this.loadComponent();
  }

  _component: Type<any>;

  settings: Settings = new Settings();
  private channelSubscription: Subscription;

  constructor(private pico: PicoEvent, private componentFactoryResolver: ComponentFactoryResolver) {
  }

  ngOnInit() {

    this.channelSubscription = this.pico.listen(SettingsMessage, evt => {
      if (evt.value) {
        this.settings = evt.value;
      } else {
        this.settings = new Settings();
      }
    });
    this.loadComponent();
  }

  private loadComponent() {
    this.adHost.viewContainerRef.clear();
    this.adHost.viewContainerRef.createComponent(this.componentFactoryResolver.resolveComponentFactory(this._component));
  }
}
