import {Component} from '@angular/core';

@Component({
  selector: 'app-radar',
  templateUrl: './radar.component.html',
  styleUrls: ['./radar.component.css']
})
export class RadarComponent {
  warpscript = ` @training/dataset0
                    [ $TOKEN '~warp.*committed' { 'cell' 'prod' } $NOW -5 ] FETCH
                    'list' STORE
                    $list
                    <%
                    DROP 'gts' STORE
                    $gts LABELS 'rack' GET 'rack' STORE

                    {
                    $rack $gts VALUES <%
                    'index' STORE 'val' STORE
                    { $index TOSTRING $val 5 % }
                    %> LMAP
                    }
                    %> LMAP
                    STOP 'values' STORE
                    { 'data' $values }`;


  sample = `<warp-view-tile url="warp 10 url" responsive="true" type="radar" unit="°C" show-legend="false" chart-title="Radar">
<< your warpscript >>
</warp-view-tile>
`;

  constructor() {
  }
}
