import {Component, OnInit} from '@angular/core';
import {PicoEvent} from 'picoevent';
import {Subscription} from 'rxjs/Subscription';
import {SettingsMessage} from '../../model/settings-message';
import {Settings} from '../../model/settings';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit {
  collapse = true;
  settings: Settings = new Settings();

  private channelSubscription: Subscription;
  autoRefreshList = [
    {val: -1, label: 'off'},
    {val: 5, label: '5s'},
    {val: 10, label: '10s'},
    {val: 30, label: '30s'},
    {val: 60, label: '1m'},
    {val: 300, label: '5m'},
    {val: 600, label: '10m'},
    {val: 1800, label: '30m'},
    {val: 3600, label: '1h'}
  ];

  constructor(private pico: PicoEvent) {
  }

  ngOnInit() {
    this.channelSubscription = this.pico.listen(SettingsMessage, evt => {
      if (evt.value) {
        this.settings = evt.value;
      } else {
        this.settings = new Settings();
      }
    });
  }

  updateSettings(e: any) {
    this.pico.publish(new SettingsMessage(this.settings));
  }
}
