import {Component, OnInit} from '@angular/core';
import {WelcomeComponent} from '../tiles/welcome/welcome.component';
import {PlotComponent} from '../tiles/plot/plot.component';
import {MapComponent} from '../tiles/map/map.component';
import {TextComponent} from '../tiles/text/text.component';
import {BubblesComponent} from '../tiles/bubbles/bubbles.component';
import {DefaultComponent} from '../tiles/default/default.component';
import {PicoEvent} from 'picoevent';
import {SettingsMessage} from '../model/settings-message';
import {Settings} from '../model/settings';
import {Subscription} from 'rxjs/Subscription';
import {StepComponent} from '../tiles/step/step.component';
import {AreaComponent} from '../tiles/area/area.component';
import {ScatterComponent} from '../tiles/scatter/scatter.component';
import {BarComponent} from '../tiles/bar/bar.component';
import {GaugeComponent} from '../tiles/gauge/gauge.component';
import {PieComponent} from '../tiles/pie/pie.component';
import {DoughnutComponent} from '../tiles/doughnut/doughnut.component';
import {PolarComponent} from '../tiles/polar/polar.component';
import {RadarComponent} from '../tiles/radar/radar.component';
import {ImageComponent} from '../tiles/image/image.component';
import {TileComponent} from '../tiles/tile/tile.component';
import {AnnotationComponent} from '../tiles/annotation/annotation.component';
import {GtsTreeComponent} from '../tiles/gts-tree/gts-tree.component';
import {DrilldownComponent} from '../tiles/drilldown/drilldown.component';
import {DatagridComponent} from '../tiles/datagrid/datagrid.component';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {
  selected = 0;
  demo: any[] = [
    {
      tabTitle: 'Getting Started',
      component: WelcomeComponent
    },
    {
      tabTitle: 'Tile',
      component: TileComponent
    },
    {
      tabTitle: 'Plot',
      component: PlotComponent
    },
    {
      tabTitle: 'Drill Down',
      component: DrilldownComponent
    },
    {
      tabTitle: 'Datagrid',
      component: DatagridComponent
    },
    {
      tabTitle: 'Annotation',
      component: AnnotationComponent
    },
    {
      tabTitle: 'GTS Tree',
      component: GtsTreeComponent
    },
    {
      tabTitle: 'Line chart (default)',
      component: DefaultComponent
    },
    {
      tabTitle: 'Step chart',
      component: StepComponent
    },
    {
      tabTitle: 'Area chart',
      component: AreaComponent
    },
    {
      tabTitle: 'Bubbles chart',
      component: BubblesComponent
    },
    {
      tabTitle: 'Scatter chart',
      component: ScatterComponent
    },
    {
      tabTitle: 'Bar chart',
      component: BarComponent
    },
    {
      tabTitle: 'Pie chart',
      component: PieComponent
    },
    {
      tabTitle: 'Gauge chart',
      component: GaugeComponent
    },
    {
      tabTitle: 'Doughnut chart',
      component: DoughnutComponent
    },
    {
      tabTitle: 'Polar chart',
      component: PolarComponent
    },
    {
      tabTitle: 'Radar chart',
      component: RadarComponent
    },
    {
      tabTitle: 'Map',
      component: MapComponent
    },
    {
      tabTitle: 'Display',
      component: TextComponent
    },
    {
      tabTitle: 'Image',
      component: ImageComponent
    },
  ];
  settings: Settings = new Settings();
  defOpts: any = {
    gridLineColor: '#000000',
    fontColor: '#000000',
    mapType: 'DEFAULT',
    showControls: false,
    showGTSTree: true,
    foldGTSTree: true,
    autoRefresh: -1
  };

  private channelSubscription: Subscription;

  constructor(private pico: PicoEvent) {
  }

  ngOnInit() {
    this.channelSubscription = this.pico.listen(SettingsMessage, evt => {
      if (evt.value) {
        this.settings = evt.value;
      } else {
        this.settings = new Settings();
      }
      if (this.settings.theme === 'dark') {
        this.defOpts.mapType = 'DEFAULT';
        this.defOpts.gridLineColor = '#ffffff';
      } else {
        this.defOpts.mapType = 'DEFAULT';
        this.defOpts.gridLineColor = '#000000';
      }
      if (this.settings.autorefresh !== this.defOpts.autoRefresh) {
        this.defOpts.autoRefresh = this.settings.autorefresh;
      }
      this.defOpts = {...this.defOpts};
    });
  }

  show(index: number) {
    this.selected = index;
  }

}
